* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(100deg, #667eea 0%, #764ba2 100%);
  font-family: 'Poppins', sans-serif;
  color: #121212;
}
.container {
  margin: 1.5%;
  height: 100vh;
}
h1 {
  text-align: center;
}
main {
  display: flex;
  flex-wrap: wrap;
  margin: 2% auto;
  justify-content: center;
}
button {
  padding: 1%;
  margin: 0.3%;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  color: #121212;
  font-weight: 600;
  background-color: #dbb407d8;
  min-width: 40px;
}
button:hover {
  background-color: #dbb407;
}
.clicked {
  background-color: rgba(235, 45, 226, 0.8);
  opacity: 0.5;
  cursor: not-allowed;
}
.clicked:hover {
  background-color: rgb(235, 45, 225);
}
.lives {
  text-align: center;
}
h4 {
  margin-top: 1%;
  text-align: center;
  font-size: 1.5rem;
}
h3 {
  font-size: 1.5rem;
}
h2 {
  font-size: 5rem;
}
.seven {
  color: rgb(3, 131, 3);
}
.six {
  color: #3acc35;
}
.five {
  color: rgb(151, 201, 15);
}
.four {
  color: rgb(250, 250, 8);
}
.three {
  color: rgb(255, 102, 0);
}
.two {
  color: rgb(255, 30, 0);
}
.one {
  color: rgb(255, 7, 7);
}
.blanks {
  width: 80%;
  display: flex;
  justify-content: center;
  margin: 2% auto;
  font-size: 6rem;
}
span {
  letter-spacing: 3rem;
}
section {
  height: 50vh;
  position: absolute;
  top: 32%;
  right: 15%;
  width: 70%;
  margin: 1% auto;
  border-radius: 5px;
  display: none;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}
.win {
  background-color: rgb(64, 172, 64);
}
.lose {
  background-color: rgb(211, 61, 61);
}
.show {
  display: block;
}
h6 {
  font-size: 3rem;
  text-align: center;
  padding-top: 3%;
}
form {
  display: flex;
  justify-content: space-around;
  margin-top: 10%;
}
form button {
  min-width: 100px;
}

@media only screen and (max-width: 1200px) {
  span {
    letter-spacing: 1.7rem;
  }
  .blanks {
    font-size: 3.5rem;
  }
  h6 {
    font-size: 1.5rem;
    padding-top: 25%;
  }
}

@media only screen and (max-width: 700px) {
  span {
    letter-spacing: 1rem;
  }
  .blanks {
    font-size: 1.8rem;
  }
}
